import logo from './logo.svg';
import React from 'react'; 
import { Container, Row, Col } from 'react-grid-system';
import './App.scss';
import ShopCard from './files/ShopCard'
import Items from './files/Items'

function App() {

    const info = Items.map(res => <Col md={4}><ShopCard title={res.name} description={res.description} price={res.price}/></Col>);

  return(<div className="App">
    <Container fluid>
    <Row>
        
        {info}
        
        
    </Row>
    </Container>
  </div>)
}

export default App;
